import { 
	Component, 
	ContentChildren, 
	QueryList, 
	AfterContentInit, 
	Input, 
	OnInit, 
	Attribute, 
	ElementRef, 
	EventEmitter } from '@angular/core';
	import { TabComponent } from '../view/tabs/tab.component';



@Component({
  selector: 'extjs',
	inputs: [ 'xtype', 'layout', 'config'],
	outputs: [ 'ready', 'select' ], //'click', 'selectionchange'],
	template: ``
})
export class ExtJS implements AfterContentInit, OnInit {
	@ContentChildren(TabComponent) tabs: QueryList<TabComponent>;

	public extjsObject: any;
	private rootElement: any;

	constructor(myElement: ElementRef) {
		this['ready'] = new EventEmitter();
		this['select'] = new EventEmitter();
		this.rootElement = myElement.nativeElement;
	}

	ngAfterContentInit() {
		// // get all active tabs
		// let activeTabs = this.tabs.filter((tab)=>tab.active);
		
		// // if there is no active tab set, activate the first
		// if(activeTabs.length === 0) {
		// 	this.selectTab(this.tabs.first);
		// }
	}

	public ngOnInit() {
		let me: any  = this;
		if (me.rootElement.childElementCount === 0) {
			let o: any = {
				xtype: me.xtype,
				layout: me.layout,
				top: 0, left:0, width: '100%', height: '100%',
				renderTo: me.rootElement
			};
			if (me.config !== {} ) {
				Ext.apply(o, me.config);
			};
			o.listeners = {};
			o.listeners['select'] = function(list, record, eOpts) {
				me.select.next({list: list, record: record, eOpts: eOpts});
			};

			me.extjsObject = Ext.create(o);
			me.ready.next(me);
		}
	}
}


